
html, body {
  width: 100%;
}
body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #ffffff;
}
@media screen and (min-width: 680px) {
body {
    width: 680px;
    position: relative;
    margin: 0 auto;
}
.app {
    width: 680px;
}
}
.app {
  padding-top: 50px;
}



