
.page-cover {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.4);
  z-index: 7;
}
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 50px;
  font-size: 1.8px;
  font-weight: 500;
  line-height: 50px;
  background-color: #03a9f4;
  color: #fff;
  padding: 0;
  text-align: center;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.header.show {
  -webkit-transform: translateX(200px);
          transform: translateX(200px);
}
.header .title {
  display: inline-block;
  margin: 0 auto;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
font
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  z-index: 2;
}
.header .pull-left {
  padding: 0 10px;
  box-sizing: border-box;
  float: left !important;
}
.header .pull-left .iconfont {
  font-size: 25px;
}
.header .pull-right {
  padding: 0 10px;
  box-sizing: border-box;
  float: right !important;
}
.header .pull-right .iconfont {
  font-size: 18px;
}


