
.menu-list {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -250px;
  width: 250px;
  background-color: #22262a;
  color: #313131;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  z-index: 99;
}
.menu-list .menu-header {
  width: 100%;
  height: 180px;
  background-color: #262d30;
}
.menu-list .menu-header .menu-avatar {
  width: 40%;
  height: auto;
  border-radius: 50%;
  margin: 20px 70px;
}
.menu-list .menu-header .menu-title {
  font-size: 14px;
  color: #fff;
  line-height: 28px;
  font-weight: 500;
  text-align: center;
}
.menu-list.show {
  -webkit-transform: translateX(250px);
          transform: translateX(250px);
}
.menu-list .menu-ul {
  margin: 0;
  overflow: hidden;
  padding: 0;
}
.menu-list .menu-ul .item {
  display: block;
  font-size: 14px;
  padding: 10px 13px;
  text-align: left;
  text-indent: 1px;
  line-height: 15px;
  color: #a6adb3;
  font-weight: 700;
}
.menu-list .menu-ul .item .menu-icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: #383c40;
  border-radius: 50%;
  vertical-align: top;
  text-align: center;
}
.menu-list .menu-ul .item .menu-icon .iconfont {
  color: #999;
  line-height: 30px;
  font-weight: 700;
}
.menu-list .menu-ul .item.active {
  color: #01aef3;
}
.menu-list .menu-ul .item.active .menu-icon {
  background-color: #01aef3;
}
.menu-list .menu-ul .item.active .menu-icon .iconfont {
  color: #fff;
}
.menu-list .menu-ul .item .menu-text {
  display: inline-block;
  font-size: 15px;
  line-height: 32px;
  vertical-align: top;
  margin-left: 10px;
  font-weight: 700;
}
.menu-list .menu-ul .item .menu-new {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: #d81229;
  border-radius: 50%;
  vertical-align: top;
  text-align: center;
  float: right;
}
.menu-list .menu-ul .item .menu-new span {
  color: #fff;
  font-size: 15px;
  line-height: 30px;
  font-weight: 500;
}


