
@media screen and (max-width: 500px) {
#content .topic_title {
    font-size: 1em;
    width: 100%;
}
}
.card {
  border-top: 1px solid #f0f0f0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  line-height: 30px;
  padding: 5px;
}
.card:hover {
  background: #f5f5f5;
}
.card .desc {
  height: 30px;
  width: 100%;
  color: #333;
  font-size: 1rem;
  font-weight: bolder;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card .info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  width: 100%;
  line-height: 20px;
  height: 20px;
}
.card .info .who {
  width: 20%;
  flex-shrik: 0;
  font-size: 0.8rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card .info .time {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  font-size: 0.8rem;
  width: 20%;
}


